.result-section {
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.result-container {
  width: 100%;
  max-width: 600px;
  text-align: center;
}

.result-container h2 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: #333;
  font-weight: 600;
}

.result-box {
  padding: 1.5rem;
  border-radius: 8px;
  font-size: 1.1rem;
  color: white;
  background-color: #6200ea; /* Default color */
  transition: background-color 0.3s ease;
}

.result-box.fake {
  background-color: #ff4081; /* Reddish-pink for fake news */
}

.result-box.real {
  background-color: #2196f3; /* Blue for real news */
}

.result-box p {
  margin: 0;
  line-height: 1.6;
}