body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f9;
  color: #333;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #6200ea;
  color: white;
}

.header .logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.header nav ul {
  list-style: none;
  display: flex;
  gap: 1rem;
}

.header nav ul li a {
  color: white;
  text-decoration: none;
}

.hero {
  text-align: center;
  padding: 4rem 2rem;
  background-color: #6200ea;
  color: white;
}

.hero h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.hero button {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: #ff4081;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.input-section {
  padding: 2rem;
  text-align: center;
}

.input-section textarea {
  width: 80%;
  height: 150px;
  padding: 1rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.input-section button {
  margin-top: 1rem;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: #ff4081;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.result-section {
  padding: 2rem;
  text-align: center;
}

.footer {
  text-align: center;
  padding: 1rem;
  background-color: #333;
  color: white;
}

.footer .social-links {
  margin-top: 0.5rem;
}

.footer .social-links a {
  color: white;
  margin: 0 0.5rem;
  text-decoration: none;
}